.container {
    margin: 0 auto;
    padding: 0 20px;

    @media screen and (min-width:320px) {
        width: 320px;
    }
    
    @include bigmobile{
      width: 480;
      padding: 0 76px;
    }

    @media screen and (min-width:768px) {
        width: 768px;
    }

    @media screen and (min-width: 1024px) {
        width: 1024px;
        padding: 0 71px;
    }

    @media screen and (min-width: 1200px) {
      width:1200px;
      padding: 0 76px;
    }
}